.list {
  max-height: 300px;
}

.default-list.is-selected-all {
  background: var(--bg-color-grey-3);
  cursor: not-allowed;
}
.default-list.is-selected-all .item-label {
  color: var(--font-color-grey-2);
}
.default-list.is-selected-all .list-item {
  pointer-events: none;
}

.default-list-item {
  padding: 5px 12px !important;
  border-bottom: none;
  cursor: pointer;
  transition: background 300ms;
  position: relative;
  border-bottom-color: transparent !important;
}
.default-list-item:not(.is-selected):hover {
  background: var(--bg-color-grey-1);
}
.default-list-item.is-selected {
  background: var(--primary-color-1);
}
.default-list-item.is-selected .default-item-label,
.default-list-item.is-selected .default-item-des,
.default-list-item.is-selected .single-checked {
  color: var(--primary-color);
}
.default-list-item.is-disabled {
  color: var(--font-color-grey-2);
  cursor: not-allowed;
}

.default-item-label,
.default-item-description {
  transition: color 300ms;
}

.default-item-description {
  color: var(--font-color-grey-1);
}

.single-checked {
  flex-basis: 14px;
}